.right-column {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    width: 220px;
    padding: 20px;
    background-color: #f8f9fa;
    border-left: 1px solid #dee2e6;
    box-sizing: border-box;
    overflow-y: auto;
}

.right-column-content {
    margin-bottom: 20px;
}

.right-column h2 {
    font-size: 1.5em;
    margin-bottom: 20px;
}

.right-column .button {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    padding: 10px;
    background-color: #6c757d;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1em;
    transition: background-color 0.3s;
    width: 100%;
    box-sizing: border-box;
}

.right-column .button .icon {
    margin-right: 10px;
}

.right-column .button:hover {
    background-color: #5a6268;
}

@media (max-width: 768px) {
    .right-column {
        width: 100%;
        padding: 10px;
        height: auto;
        overflow-y: visible;
    }
}









